/*Header*/
.header {
    grid-area: h;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: brown;
  }
  
  .header img {
    margin-left: 8px;
    height: 90%;
  }

  .loginBlock {
      display: inline-block;
  }

.NavLink {
    margin-right: 8px;
    font-size: 20px;
    font-weight: 700;
    color: black;
    text-decoration: none;
    transition: 0.3s;
}